[edit-on-click-duration],
[edit-on-click] {
  cursor: pointer;
}

.edit-on-click-form {
  display: inline-block;
  &.duration-form {
    width: auto;
  }

  input {
    display: inline-block;
  }
  input.edit-on-click-duration-input {
    width: 70px;
    padding-left: 2px;
    padding-right: 2px;
  }
}

.edit-on-click-text {
  display: inline-block;
}